.about {
  &__content {
    h4 {
      @extend %mb-20;
    }

    p {
      @include font-size(18px);
    }
  }

  &__list {
    &-item {
      &::before {
        content: '0';
      }

      @include font-size(18px);
      color:$title-color-dark;
      font-weight: 600;
    }
  }
}
