// ======== Responsive Variables
$desktop: "only screen and (min-width: 1400px)";
$laptop: "only screen and (min-width: 1200px) and (max-width: 1399px)";
$lg: "only screen and (min-width: 992px) and (max-width: 1199px)";
$md: "only screen and (min-width: 768px) and (max-width: 991px)";
$xs: "(max-width: 767px)";
$sm: "only screen and (min-width: 550px) and (max-width: 767px)";

$gray: #5d657b;

/* ========== header css ========== */

.header {
  background-color: $white-color;
  border-radius: 1rem;
  padding-block: 15px;
  position: sticky;
  border: 1px solid rgba($brand-color, $alpha:.1);
  top: 0;
  z-index: 9;

  @include breakpoint(xl) {
    top: #{$body-gap};
    padding-block: 20px;
    padding-inline: 8px;
  }

  &__wrapper {
    @extend %flex,
    %align-i-center,
    %justify-between;
  }

  &__left {
    gap: 30px;

    .menu-toggle {
      &__btn {
        @extend %p-rel,
        %of-hidden,
        %c-pointer;
        @include box(40px);
        @include add-prefix(transition, $transition);
        background-color: $primary-color-dark;
        border-radius: .5rem;
        text-indent: 100%;
        color: transparent;
        white-space: nowrap;
        outline: none;
        box-shadow: none;
        border: none;



        span {
          @include add-prefix(transition, $transition);
          top: calc(50% - 2px);
          left: calc(50% - 12px);

          &,
          &::after,
          &::before {
            @extend %p-abs;
            @include add-prefix(transition, $transition);
            content: "";
            background: white;
            border-radius: 3px;
            height: 3px;
            width: 25px;
          }

          &::after,
          &::before {
            left: 0;
          }


          &:before {
            bottom: 8px;
          }

          &:after {
            top: 8px;
          }
        }

        @include breakpoint(lg) {
          @include box(50px);
          border-radius: .75rem;
        }

        &:hover {
          span {

            @include breakpoint(xl) {
              @include add-prefix(transform, rotate(180deg));
            }

            &.open-menu {
              @include add-prefix(transform, rotate(0));
            }


            &::before {
              @include add-prefix(transform, translateX(13px) translateY(3px) rotate(45deg));
              width: 50%;
            }

            &::after {
              @include add-prefix(transform, translateX(13px) translateY(-3px) rotate(-45deg));
              width: 50%;
            }
          }
        }
      }
    }

    .header__search {
      form {
        @extend %p-rel;
        max-width: 270px;

        input {
          @extend %w-100;
          @include add-prefix(transition, $transition);
          border: 1px solid $border-color;
          background: rgba($light, 0.5);
          border-radius: 10px;
          height: 45px;
          padding-inline: 15px 40px;


          &:focus {
            border-color: $brand-color;
            background: $white-color;
            box-shadow: none;
            outline: none;
          }
        }

        button {
          @include box(45px);
          position: absolute;
          border: none;
          background: transparent;
          right: 0;
          top: 0;
          color: $gray;
          font-weight: 700;
        }
      }
    }
  }

  &__actions {
    @extend %flex,
    %align-i-center;
    gap: 10px;
  }

  &__right {
    display: flex;
    justify-content: flex-end;
    gap: 20px;

    .dropdown-toggle {

      @extend %d-grid,
      %place-i-center,
      %p-rel;
      border: 1px solid $border-color;
      background: rgba($brand-color, 0.2);
      border-radius: 10px;

      &:not(.author-toggle) {
        @include box(35px);
      }

      &::after {
        display: none;
      }

      & span {
        @extend %p-abs,
        %d-grid,
        %place-i-center;
        @include box(16px);
        @include font-size(10px);
        background: $primary;
        color: $white;
        border-radius: 50%;
        top: -8px;
        right: -6px;
        font-weight: 500;
      }

      &.flag-toggle {
        background-color: transparent;
        border: none;
      }

      @include breakpoint(md) {
        & span {
          @include box(20px);
          @include font-size(12px);
        }
      }

      @include breakpoint(lg) {
        @include box(46px);
      }

    }

    .dropdown-menu {
      @extend %p-abs,
      %p-10;
      @include add-prefix(transition, $transition);
      border: 1px solid $border-color;
      border-radius: 10px;
      box-shadow: 0 5px 10px rgba($brand-color, $alpha:.1);
      background-color: $white-color;
      right: 0;
      position: absolute;
      top: 24px !important;
      transform: translate3d(0px, 60px, 0px);
      width: 240px;

      @include breakpoint(md) {
        width: 350px;
      }

      li {
        @include add-prefix(transition, $transition);
        border-bottom: 1px solid $border-color;
        padding: 3px 0px;
        position: relative;
        z-index: 2;

        &:hover {
          a {
            color: $primary;
            background: rgba($primary, 0.05);
          }
        }

        &:last-child {
          border-bottom: none;
        }

        a {
          padding: 8px 12px;
          display: flex;
          color: $black-color;
          border-radius: 6px;

          .image {
            max-width: 35px;
            width: 100%;
            height: 35px;
            border-radius: 50%;
            overflow: hidden;
            margin-right: 12px;

            img {
              width: 100%;
            }
          }

          .content {
            width: 100%;

            h6 {
              font-size: 14px;
              margin-bottom: 5px;
              font-weight: 600;
              line-height: 1;
            }

            p {
              font-size: 14px;
              color: $text-color;
              margin-bottom: 0px;
              line-height: 1.4;
            }

            span {
              font-size: 12px;
              color: $title-color;
            }
          }
        }
      }
    }

    .dropdown-box {
      position: relative;
    }

    .header__language {
      .dropdown-menu {
        width: 200px;
      }
    }

    .notification-box,
    .header__language {
      position: relative;
    }

    .notification-box {
      .dropdown-menu {
        &.dropdown-menu-end {
          // transform: translate3d(0px, 60px, 0px);
        }
      }
    }

    .header__language {
      &-image {
        @include box(25px);
      }

      &-name {
        p {
          @extend %mb-0;
        }
      }

      .dropdown-menu {
        &.dropdown-menu-end {
          // transform: translate3d(0px, 60px, 0px);
        }

        a {
          gap: 10px;
        }
      }
    }

    .profile {
      display: flex;
      position: relative;

      button {
        width: auto;
      }

      .dropdown-menu {
        width: 230px;

        &.dropdown-menu-end {
          // transform: translate3d(0px, 60px, 0px);
        }

        li {
          border-bottom: none;

          a {
            font-size: 14px;
            display: flex;
            align-items: center;

            i {
              margin-right: 15px;
              font-weight: 700;
            }
          }
        }
      }

      &__info {
        margin: 0 5px;
        display: flex;
        align-items: center;
        gap: 10px;

        >i {
          @extend %d-none;

          @include breakpoint(md) {
            display: inline-block;
          }
        }
      }

      &__avatar {
        @include box-shadow(0px 21px 25px rgba(218, 223, 227, 0.8));
        width: 46px;
        height: 46px;
        border-radius: 50%;
        position: relative;

        img {
          width: 100%;
          border-radius: 50%;
        }

        .status {
          width: 15px;
          height: 15px;
          border-radius: 50%;
          border: 2px solid rgba($brand-color, $alpha:.2);
          background: $success;
          position: absolute;
          bottom: 0;
          right: 0;
          top: auto;
        }
      }

      &__name {
        @extend %d-none;

        @include breakpoint(md) {
          display: block
        }

        h6 {
          @extend %mb-0;
          @include font-size(16px);
        }

        p {
          @extend %mb-0;
          @include font-size(14px);
          color: $text-color;
        }
      }
    }
  }
}